.content {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(126, 126, 126, 0.45)), to(rgba(26, 26, 26, 1))),
    url(../assets/monster_hunter.jpg);
  background: -webkit-linear-gradient(rgba(126, 126, 126, 0.45), rgba(26, 26, 26, 1)),
    url(../assets/monster_hunter.jpg);
  background: -o-linear-gradient(rgba(126, 126, 126, 0.45), rgba(26, 26, 26, 1)),
    url(../assets/monster_hunter.jpg);
  background: linear-gradient(rgba(126, 126, 126, 0.45), rgba(26, 26, 26, 1)),
    url(../assets/monster_hunter.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  min-height: 100%;
  /* box-shadow: inset 1px 1px 1px 1000px rgba(0, 0, 0, 0.7); */
}

.streamer-container .flex-col4 img {
  margin-right: auto;
  margin-left: auto;
  max-width: 70%;
  border-radius: 10pc;
  -webkit-transform: skewX(10deg);
      -ms-transform: skewX(10deg);
          transform: skewX(10deg);
}

.container .flex-col4 {
  display: inline-block;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  -webkit-transform: skewX(-10deg);
      -ms-transform: skewX(-10deg);
          transform: skewX(-10deg);
  -webkit-box-shadow: 1px 1px 15px rgb(59, 59, 59);
          box-shadow: 1px 1px 15px rgb(59, 59, 59);
  margin: 0px 10px 0px 10px;
  background-image: url(../assets/carbon_background.jpg);
  opacity: 0.8;
  border-radius: 0;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.container .flex-col4:hover {
  -webkit-box-shadow: 2px 2px 25px rgb(37, 37, 37);
          box-shadow: 2px 2px 25px rgb(37, 37, 37);
  opacity: 1;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.container .flex-col4 p {
  -webkit-transform: skewX(10deg);
      -ms-transform: skewX(10deg);
          transform: skewX(10deg);
}

.con .flex-col4 {
  width: 100%;
}

.stream-container {
  text-align: center;
}



